*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  /* list-style: none; */
  text-decoration: none;
}

:root{
  --color-bg:#0c0c29;

  /* Testing */
  /* --color-bg:rgba(146, 46, 157, 0.979); */
  /* --color-white:rgba(242, 243, 245, 0.932); */



