/* Genera; styles */

.container
{
  
  width:var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{

  font-weight: 500;
  
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  /* height: 100vh; */
  /* margin-bottom: 8rem; */
}

section>h2,section>h5{
  text-align: center;
  color: var(--color-light);
}
section>h2
{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-primary);
}

a{
  /* color: var(--color-primary); */
  color: var(--color-white);
  transition: var(--transition);
}

a:hover{
  
  /* color: var(--color-white); */
  color: rgb(251,251,251);
}
.btn{
  
  width: max-content;
  display: inline-block;

  /* background: #fff; */
   
  color: rgb(151, 77, 231);;
  
  /* background: #0A3D8A;
background: repeating-radial-gradient(ellipse farthest-side at center center, #0A3D8A 0%, #20990E 54%, #960C48 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent; */

  
  

  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor:pointer;
  border: 1px solid rgb(151, 77, 231);;
  transition: var(--transition);
}

.btn:hover{

  
  letter-spacing: 0.13rem;
  box-shadow: 0 0 45px var(--color-white);
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background: rgb(151, 77, 231);;
  color: var(--color-bg);
}
img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media Queries for Tablets (Medium size screen) */


@media screen and (max-width:1024px) {
  .container{
    width: var(--container-width-md);

  }
  section{
    margin-top: 6rem;
  }
  
}

/* Media Queries for Mobile Phones (Small Devices) */

@media screen and (max-width:600px)
{
  .container{
    width: var(--container-width-sm);
  }
  section > h2{
    margin-bottom: 2rem;
  }
}

@media only screen and (max-height: 575.98px) and (orientation: landscape) {

  .container{
    width: var(--container-width-md);
  }
  section
  .header__container{
      background: aqua;
  }
  header{
      /* width:vh; */
      padding:10px;
      height:68vh;
  }
  h1,h2,h3,h4,h5{
    font-weight: 600;
    
  }

  }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
