/* ========================CTA CSS========================= */

.cta{
    
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}


/* ========================Header Socials========================= */

.header__socials{

    display: flex;
    flex-direction: column;
    align-items: center;
    /* gap: 0.1rem; */
    position: absolute;
    /* position: fixed; */
    /* left: 3rem; */
    left: 0;
    bottom: 0rem;
}

.header__socials a 
{

    /* font-size: 50px; */
    padding: 5px;
    padding-top: 12px;
    /* border: 1px solid white; */
    border-radius: 50% 0% 50% 0%;
    transition: 0.3s ease-in;
    /* position: relative; */
}

.header__socials:hover a{
    opacity: 0.1;
}
.header__socials>a:hover
{
    opacity: 1;
}
.header__socials a:hover:nth-child(1)
{
    background:#4978df;
    box-shadow: 0 3px 30px 0 #4978df inset,
                0 3px 30px 0 #4978df,
                0 3px 30px 0 #4978df inset,
                0 3px 30px 0 #4978df;
    text-shadow:0 0 5px #4978df,
                0 0 5px #4978df;
}
.header__socials a:hover:nth-child(2)
{
    background:rgb(107, 10, 218);
    box-shadow: 0 3px 30px 0 rgb(107, 10, 218) inset,
                0 3px 30px 0 rgb(107, 10, 218),
                0 3px 30px 0 rgb(107, 10, 218) inset,
                0 3px 30px 0 rgb(107, 10, 218);
    text-shadow:0 0 5px rgb(107, 10, 218),
                0 0 5px rgb(107, 10, 218);
}

.header__socials a:hover:nth-child(3)
{
    background: #25d366;;
    box-shadow: 0 3px 30px 0 #25d366 inset,
                0 3px 30px 0 #25d366,
                0 3px 30px 0 #25d366 inset,
                0 3px 30px 0 #25d366;
    text-shadow:0 0 5px #25d366,
                0 0 5px #25d366;
}
.header__socials a:hover:nth-child(4)
{
    background:#c13584;
    box-shadow: 0 3px 30px 0 #c13584 inset,
                0 3px 30px 0 #c13584,
                0 3px 30px 0 #c13584 inset,
                0 3px 30px 0 #c13584;
    text-shadow:0 0 5px #c13584,
                0 0 5px #c13584;
}
.header__socials a:hover:nth-child(5)
{
    background:#00acee;
    box-shadow: 0 3px 30px 0 #00acee inset,
                0 3px 30px 0 #00acee,
                0 3px 30px 0 #00acee inset,
                0 3px 30px 0 #00acee;
    text-shadow:0 0 5px #00acee,
                0 0 5px #00acee;
}


.header__socials::after{
    content: " ";
    width: 1px;
    height: 5rem;
    /* background: var(--color-primary); */
    background: var(--color-white);
}


/* ========================ME( image )========================= */

.me{
    background: linear-gradient(rgba(235, 235, 243, 0.683),transparent);
    width: 22rem;
    height: 35rem; 
    position:absolute;
    left:calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2rem 1.5rem 1.5rem 1.5rem;

}

.logo{
    width: 4rem;
    margin-top: 0rem;
    padding-top: 0;
    height: 0rem;
    position: absolute;

}

/* ========================Scroll Down========================= */

.scroll__down{
    color: rgb(95, 90, 90);
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

#scroll:hover{
    color: rgb(95, 90, 90);
}

/* Media Queries (Medium Devices) */

@media screen and (max-width:1024px)
{
    /* section{
        margin-bottom: 40rem;
    } */

    header{
        margin-top: 0;
        height:68vh;
    }
}

/* Media Queries (Small Devices) */

@media screen and (max-width:600px) {
    header
    {
        height: 100vh;
    }

    .header__socials,.scroll__down{
        display: none;
    }
}

@media only screen and (max-height: 575.98px) and (orientation: landscape) {

    .header__container{
        /* background: greenyellow; */
        font-size: small;
    }
    header{
        padding:10px;
        height:103vh;
    } 
    .me{
        width: 15rem;
        height: 13rem; 
        left:calc(50% - 8rem);
         margin-top: 1rem; 
    }
    h1{
        font-size:30px;
    }
    .cta{
        margin-top: 0.5rem;
        gap: 1.2rem;
    }
    .scroll__down{
        right: -2.0rem;
        bottom: 2rem;
        font-size: 0.8rem;
    }
    .header__socials{
        gap: 0.2rem;
        left: 0.3rem;
        bottom: 1rem;
    }
    
    .header__socials::after{
        content: " ";
        width: 1px;
    }
    

}


