.nav__bar{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
.dock{
    /* background: red; */
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.5rem;
}

.dock:hover{
    background: rgba(0, 0, 0, 0.3);
}

.dock.active{
    background: var(--color-white);
    box-shadow: 0 0 45px var(--color-white);
    color: var(--color-bg);
}