.about__container{
    text-align: justify;
    display: grid;
    grid-template-columns: 50%  35%;
    gap: 15%;
}
.about__me{
    width: 100%;
    height: 74vh;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    /* background-color: #FF3CAC;
background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%); */

background: #114357;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #F29492, #114357);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #F29492, #114357); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    /* background: linear-gradient(67deg,transparent,var(--color-primary),transparent); */
    display: grid;
    place-items: center;
}

/* .about__me{
    width: 47%;
    height: 100%;
    margin: 2rem auto 4rem;
}
.about__me-image{
    width: 75%;
} */

.about__me-image{
    
    width: 90%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(15deg);
    transition:var(--transition);
}




.about__me-image:hover{
    transform: rotate(0);
}


.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}
.about__card{
    /* background:var(--color-bg-variant); */
    /* background-image: radial-gradient(); */
    /* background: rgb(2,0,36);
background: radial-gradient(circle, rgba(2,0,36,1) 0%, rgba(177,134,212,0.9500175070028011) 47%, rgba(0,212,255,1) 100%); */
/* background: rgb(238,174,202);
background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);     */


/* background: rgb(236,211,222);
background: radial-gradient(circle, rgba(236,211,222,1) 0%, rgba(148,187,233,1) 88%); */

background:var(--color-bg-variant);

/* background-color: #FF3CAC;
background-image: linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%); */


border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
.about__card:hover{
    background: rgba(0, 0, 0, 0.352);
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5
{
    color: var(--color-white);
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 0.9rem;
    color: var(--color-light);
}
.about__content ul{
    display: grid;
    grid-template-columns: 50% 50%;
    list-style: disc;
}
/* .about__content{
    display: flex;
    gap: 2.8rem;
} */


@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 47%;
        height: 100%;
        margin: 2rem auto 4rem;
    }
    .about__me-image{
        width: 75%;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width:600px) {


    .about__me{
        width: 47%;
        height: 100%;
        margin: 20px auto 3rem;
    }
    .about__me-image
    {
        /* margin */
        /* position: absolute; */
        width:75%;
        /* margin-bottom: 3rem; */
    }
    .about__cards{
        grid-template-columns: 0.4fr 0.4fr;
        gap: 1rem ;
    }
    .about__content{
        /* margin-bottom: 30rem; */
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
    
}