footer{
    background-color: rgba(0, 0, 0, 0.2);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    
    color: var(--color-bg);
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks
{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    list-style: none;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials li{
    list-style: none;
}

.footer__socials li a{
    /* background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent; */

    width: 80px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    margin: 0 10px;
    text-decoration: none;
    font-size: 30px;
    border-radius: 50%;
    position: relative;
    color: #e2e2e2;
    box-shadow: inset -1px -1px 2px rgba(0,0,0,0.2)
                2px 2px 5px rgba(0, 0, 0, 0.2) ;
}

.footer__socials a:hover{
    /* background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg); */
    color: #fff;
    transform: translateY(-20px);
    box-shadow: inset -1px -1px 2px rgba(0,0,0,0.2)
    2px 24px 10px rgba(0, 0, 0, 0.2) ;
}

.footer__socials a:before{
    content: '';
    position: absolute;
    top:10px;
    left:10px;
    right: 10px;
    bottom: 10px;
    border-radius: 50%;
    border: 2px solid rgba(0,0,0,0.1);

}
.footer__socials-icons{
    position: relative;
    z-index: 1;

}
.footer__socials li:nth-child(1) a:hover
{
    background:#fff;
    box-shadow: 0 3px 30px 0 #fff inset,
                0 3px 30px 0 #25d366,
                0 3px 30px 0 #25d366 inset,
                0 3px 30px 0 #fff;
    text-shadow:0 0 5px #fff,
                0 0 5px #fff;
}
.footer__socials li:nth-child(2) a:hover
{
    background:#fff;
    box-shadow: 0 3px 30px 0 #fff inset,
                0 3px 30px 0 #c13584,
                0 3px 30px 0 #c13584 inset,
                0 3px 30px 0 #fff;
    text-shadow:0 0 5px #fff,
                0 0 5px #fff;
}
.footer__socials li:nth-child(3) a:hover
{
    background:#fff;
    box-shadow: 0 3px 30px 0 #fff inset,
                0 3px 30px 0 #668cdf,
                0 3px 30px 0 #668cdf inset,
                0 3px 30px 0 #fff;
    text-shadow:0 0 5px #fff,
                0 0 5px #fff;
}
.footer__socials li:nth-child(1) a:before{
    background:  #25d366;
}
.footer__socials li:nth-child(2) a:before{
    background:  #c32aa3;
} 
.footer__socials li:nth-child(3) a:before{
    background: #1da1f2;
}
.footer__copyright
{
    margin-bottom:4rem;
    color: var(--color-bg);
}

@media screen and (max-width:600px) {
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer__socials{
        margin-bottom: 2.6rem;
    }
}